.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1em;
  border: 2px solid #626f71;
  border-radius: 10px;
}

.card:hover {
  opacity: 0.7;
  cursor: pointer;
}

.card.active {
  border-color: red;
}

.card__heading {
  margin-bottom: 1em;
}

.card__text span {
  margin-bottom: 0.3em;
}

.card__img-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% + 2em);
  height: 260px;
  margin-left: -1em;
  margin-right: -1em;
  margin-bottom: 1.2em;
  background-color: #ffffff;
}

.card__text {
  display: flex;
  flex-direction: column;
  align-items: center;
}
