.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 2px;
  padding: 5px 9px;

  color: #ffffff;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 77.5%;

  letter-spacing: -0.04em;

  background-color: transparent;
  border: #ffffff solid 3px;
  border-radius: 5px;
  cursor: pointer;

}

.btn:hover {
  opacity: 0.7;
}
