.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0;
}

.header__logo {
  display: flex;
  align-items: center;
  margin-top: 2em;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2em;
}

.header svg {
  width: 70px;
  height: auto;
  margin-right: 1em;
}

.header span {
  position: absolute;
  top: 1em;
  left: -0.2em;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border: #ffffff solid 2px;
  border-radius: 50%;
  background-color: #17181c;
}

.header span.active {
  background-color: #f08080;
}

.header h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 3rem;
  font-weight: 800;
}

.header__nav a:not(:last-child) {
  margin-right: 1em;
}
